.preloader {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 1;
  top: 0;
  left: 0; }
  .preloader::before {
    content: '';
    background: url(../../img/common/preloader.gif) no-repeat transparent;
    margin: 0 auto;
    display: block;
    width: 64px;
    height: 64px;
    position: relative;
    top: 50%;
    top: calc(50% - (64px / 2)); }
